html{
  min-height: 100%;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background:url(/image/ridesharebg.gif) no-repeat;
  background-size:100%;
  background-position:bottom;
  background-color:#95e8f3;
  min-height: 100%;
}
#root{
  overflow-x: hidden;
}
form{padding:20px;margin-top:60px;margin-bottom: 30px;}
form button,form h5{margin:20px 0}
#age{
  display: block;
}
.input-field.radiobutton input{
  opacity:1;
  position: relative;
  pointer-events: all;
 margin: 10px;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
 .side-filters{
  margin:4vw 1vw;
  align-self: flex-start;
  width: max-content;
}
.posts-container{
  align-self: flex-start;
  margin: 4vw 4vw 4vw 0vw;
  width: 100%;
 
  min-width:300px !important; 
} 
.main-container{
  display: flex;
  padding:10px;
  align-items: flex-start;
  flex: 2 1 auto;
  /* flex-flow: row nowrap; */
}
.input-field{
  margin-bottom: 1.5rem;
}
@media only screen and (min-width: 993px){
.container.main {
    width: 80%;
}
}
.col.s12.m6.dashboard{
  width: 70%;

}
.col.s12.m5.offset-m1.notifications{
  width: 25%;

}
@media only screen and (min-width: 601px){
.row .col.offset-m1 {
  margin-left:5%;
}
}

/* smallest screen size */

@media only screen and (max-width: 375px){
nav ul a{
  font-size: 1rem !important;
}
nav ul.right li a.link{
  padding:0px 5px !important;
}
.brand-logo{
left: 20% !important;
font-size:1rem !important;
}
.fa-car-side{
  line-height: 1.5 !important;
  font-size: 24px !important;
}
.main-container.shadow{
  max-width:340px !important; 
  min-width:340px !important;
}
.post-list.section{ 
  max-width: 350px !important;
}
.posts-container{
max-width: 300px !important;
margin: 4vw 0vw 4vw 0vw !important;
}
.side-filters{
 margin:4vw 0vw !important;
 max-width:100px !important;
}

.buttons{
max-width:100px !important;
width:100px !important;
}
/* div.card.notification-div{
max-width: 150px;


} */
div.card.notification-div .card-title{
font-size:14px !important;
}
.click-options{
left:50px;
}
.MuiAppBar-root,.post-list{
  max-width:240px !important;
}
/* .card.notification-div{
max-width: 120px !important;
} */
.col.s12.m5.offset-m1.notifications{
  width: 100% !important;
  top:50px;
  margin-right:10px;
  position:fixed;

}
.col.s12.m6.dashboard {
    width: 100% !important;
    margin-top: 300px !important;

}
.post-link{
  
  margin-right:0px !important;

}
#delete-btn{
  margin-right: 60px !important;
}
.post-summary{
  max-width:146px !important;
}
.input-field label{
  margin-right:-15px !important;
}
}

/* medium screen size */

@media only screen and (min-width:376px) and (max-width: 768px){
  .main-container.shadow{
    max-width:420px !important; 
    min-width:420px !important;
  }
 .post-list.section{ 
    max-width: 350px !important;
 }
 .posts-container{
  max-width: 300px !important;
  margin: 4vw 0vw 4vw 0vw !important;
 }
 .side-filters{
   margin:4vw 0vw !important;
   max-width:100px !important;
 }
 .brand-logo {
    left:20% !important;
 }
 .buttons{
  max-width:100px !important;
  width:100px !important;
}
div.card.notification-div{
  max-width: 150px;
  

}
div.card.notification-div .card-title{
  font-size:14px !important;
}
.click-options{
left:50px;
}
nav ul a{
  font-size: 1rem;
}

.input-field label{
  margin-right:-20px !important;
}
}




.shadow {
  -moz-box-shadow:    0 0 5px #000000;
  -webkit-box-shadow:  0 0 5px #000000;
  box-shadow:         0 0 5px #000000;
}
.main-container.shadow{
  background: #ffffff;
  opacity: 0.9;
}
.card.z-depth-0.post-summary{
background:#ee6e73;
}
.MuiInput-underline:before{
  border-bottom: none !important;
}
.MuiInput-underline:after{
  border-bottom: none !important;
}
.MuiFormControl-root{
width: 100%;
}
.post-summary .card-content{
